        }"
      ></div>
    </div>
    
    <div class="galaxy-container">
      <!-- Центральное изображение (HERO) -->
      <div class="hero-image-container">
        <img src="@/assets/NFT-hero.png" alt="NFT Space" class="hero-image" style="border-radius: 0 !important; -webkit-border-radius: 0 !important; -moz-border-radius: 0 !important; filter: none !important;" />
