<script setup>
import { ref, onMounted, onUnmounted, provide } from 'vue';
import NftSpaceModule from '@/components/pages/nft-space/NftSpaceModule.vue';
import nftMint from '@/assets/nft-mint.png';
import nftRent from '@/assets/nft-rent.png';
import nftLaunch from '@/assets/nft-launch.png';
import nftMarket from '@/assets/nft-market.png';

// Создаем глобальное состояние для активного модуля
const activeModuleId = ref(null);

// Функция для установки активного модуля
const setActiveModule = (id) => {
  activeModuleId.value = id;
};

// Генерация звезд в верхней части страницы
const stars = ref([]);

// Интервал для периодической регенерации звезд
let starsInterval = null;

// Функция для генерации новой звезды в случайном месте
const generateStar = (id) => {
  return {
    id,
    top: Math.random() * 250, // Только в верхней части до 250px
    left: Math.random() * 100,
    size: Math.random() * 2 + 2, // Размер от 2 до 4px
    delay: Math.random() * 4, // Задержка анимации от 0 до 4 секунд
    opacity: Math.random() * 0.3 + 0.7 // Прозрачность от 0.7 до 1.0
  };
};

// Генерация начального набора звезд
const generateStars = () => {
  // Увеличиваем количество звезд, чтобы всегда были видимые звезды
  const starCount = Math.floor(Math.random() * 10) + 35; // От 35 до 45 звезд
  const newStars = [];
  
  // Создаем звезды с разными задержками анимации
  for (let i = 0; i < starCount; i++) {
    newStars.push(generateStar(i));
  }
  
  stars.value = newStars;
};

// Перемещение звезд в новые случайные места
const moveStars = () => {
  // Выбираем меньшее количество звезд для перемещения
  // чтобы всегда оставались видимые звезды
  const starsToMove = Math.floor(Math.random() * 3) + 1; // От 1 до 3 звезд за раз
  
  // Создаем копию текущих звезд
  const updatedStars = [...stars.value];
  
  // Выбираем случайные звезды и перемещаем их
  for (let i = 0; i < starsToMove; i++) {
    const randomIndex = Math.floor(Math.random() * updatedStars.length);
    
    // Создаем новую звезду с разными задержками анимации
    // чтобы они не мерцали все одновременно
    updatedStars[randomIndex] = generateStar(updatedStars[randomIndex].id);
  }
  
  // Обновляем звезды
  stars.value = updatedStars;
};

// Предоставляем состояние и функцию для дочерних компонентов
provide('activeModuleId', activeModuleId);
provide('setActiveModule', setActiveModule);

onMounted(() => {
  // Генерируем звезды при загрузке
  generateStars();
  
    // Устанавливаем интервал для перемещения звезд в новые места
  // Каждые 1.5 секунды несколько звезд будут перемещаться в новые места
  starsInterval = setInterval(() => {
    moveStars();
  }, 1500);
  
  // Анимация появления элементов при загрузке страницы
  const modules = document.querySelectorAll('.space-module');
  modules.forEach((module, index) => {
    setTimeout(() => {
      module.classList.add('visible');
    }, 300 + index * 200);
  });
});

onUnmounted(() => {
  if (starsInterval) {
    clearInterval(starsInterval);
    starsInterval = null;
  }
});
</script>

