<template>
  <div class="module-wrapper">
    <div 
      ref="moduleRef"
      class="space-module" 
      :class="{ 'visible': isVisible, 'hover': isHovered || (isMobile && isActive) }" 
      @mouseenter="!isMobile && (isHovered = true)" 
      @mouseleave="!isMobile && (isHovered = false)"
      @click="handleModuleClick"
    >
      <!-- Изображение модуля (всегда видно) -->
      <div class="module-image-container">
        <img :src="image" alt="Module image" class="module-image" :class="{ 'invert': isDarkMode }"/>
      </div>
      
      <!-- Информация о модуле (появляется при наведении или клике на мобильных) -->
      <div class="module-info do-invert" :class="{ 'show': isHovered || (isMobile && isActive), 'mobile-view': isMobile }">
        <h3>{{ title }}</h3>
        <p>{{ description }}</p>
        <router-link :to="route" class="module-button">
          <span>Enter</span>
          <i :class="`fas fa-${icon}`"></i>
        </router-link>
      </div>
      
      <!-- Свечение при наведении -->
      <div class="module-glow"></div>
    </div>
    <!-- Подпись под модулем -->
    <router-link :to="route" class="module-label" :class="{ 'mobile-hidden': isMobile }">{{ label }}</router-link>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, onUnmounted, inject } from 'vue';

// Props
const props = defineProps({
  title: {
    type: String,
    required: true
  },
  label: {
    type: String,
    required: true
  },
  description: {
    type: String,
    required: true
  },
  icon: {
    type: String,
    required: true
  },
  route: {
    type: String,
    required: true
  },
  image: {
    type: String,
    required: true
  }
});

// Создаем уникальный ID для этого модуля
const moduleId = Symbol();

// Получаем глобальное состояние активного модуля или создаем новое
const activeModuleId = inject('activeModuleId', ref(null));
const setActiveModule = inject('setActiveModule', null);

// Reactive state
const isHovered = ref(false);
const isVisible = ref(false);
const isMobile = ref(false);
const isClicked = ref(false);

const isDarkMode = ref(localStorage.getItem('theme') === 'dark');

const checkThemeInterval = setInterval(() => {
  isDarkMode.value = localStorage.getItem('theme') === 'dark';
}, 100);

// Проверяем, является ли этот модуль активным
const isActive = computed(() => activeModuleId.value === moduleId);

// Обработка клика по модулю на мобильных устройствах
const handleModuleClick = () => {
  if (isMobile.value) {
    // Если этот модуль уже активен, то закрываем его
    if (isActive.value) {
      setActiveModule(null);
    } else {
      // Иначе делаем этот модуль активным
      setActiveModule(moduleId);
    }
  }
};

// Проверка мобильного устройства
const checkMobile = () => {
  isMobile.value = window.innerWidth <= 768;
};

// Ref для доступа к DOM-элементу модуля
const moduleRef = ref(null);

// Intersection Observer для отслеживания видимости модуля
let observer = null;

// Функция для создания и настройки Intersection Observer
const setupIntersectionObserver = () => {
  if (!isMobile.value || !moduleRef.value) return;
  
  // Опции для Intersection Observer
  const options = {
    root: null, // viewport
    rootMargin: '-20% 0px', // активируем, когда модуль находится примерно в середине экрана
    threshold: 0.5 // срабатывает, когда 50% модуля видно
  };
  
  // Создаем наблюдатель
  observer = new IntersectionObserver((entries) => {
    const [entry] = entries;
    if (entry.isIntersecting && !isActive.value) {
      // Если модуль виден и не активен, активируем его
      handleModuleClick();
    }
  }, options);
  
  // Начинаем наблюдение
  observer.observe(moduleRef.value);
};

// Lifecycle hooks
onMounted(() => {
  // Проверка мобильного устройства
  checkMobile();
  window.addEventListener('resize', checkMobile);
  
  // Delay visibility to allow for staggered animation from parent
  setTimeout(() => {
    isVisible.value = true;
    // Настраиваем Intersection Observer после того, как элемент стал видимым
    setupIntersectionObserver();
  }, 100);
});

// Очистка при размонтировании компонента
onUnmounted(() => {
  window.removeEventListener('resize', checkMobile);
  if (observer) {
    observer.disconnect();
    observer = null;
  }

  clearInterval(checkThemeInterval);
});
</script>

